import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../service/common.service';


@Component({
  selector: 'app-hymns',
  templateUrl: './hymns.component.html',
  styleUrls: ['./hymns.component.css']
})
export class HymnsComponent implements OnInit {

  constructor(private commonService: CommonService){}
  hymnsHeaderList = [];
  hymnsValueList = [];

  ngOnInit() {
    this.commonService.getPrayerList().subscribe(data => {
      
      var headerTempList, valTempList = "";

      data.forEach(function (pitem){
        if(pitem.key == "hymns_header")
        {
          headerTempList = pitem.payload.val();
        }

        if(pitem.key == "hymns_content")
        {
          valTempList = pitem.payload.val();
        }
      });

      this.hymnsHeaderList = headerTempList.split(',');

      var re = /\|/gi;
      var tList = valTempList.replace(re,"\n\n");
      this.hymnsValueList = tList.split(',');
    });
  }

}
