import { Component, OnInit } from '@angular/core';
import { CommonService } from '../../service/common.service';

@Component({
  selector: 'app-psalms',
  templateUrl: './psalms.component.html',
  styleUrls: ['./psalms.component.css']
})

export class PsalmsComponent implements OnInit {

  constructor(private commonService: CommonService){}
  psalmsHeaderList = [];
  psalmsValueList = [];

  ngOnInit() {
  
    this.commonService.getPrayerList().subscribe(data => {
      
      var headerTempList, valTempList = "";

      data.forEach(function (pitem){
        
        if(pitem.key == "psalm_header")
        {
          headerTempList = pitem.payload.val();
        }

        if(pitem.key == "psalm_content")
        {
          valTempList = pitem.payload.val();
        }
      });

      this.psalmsHeaderList = headerTempList.split(',');
      
      var re = /\|/gi;
      var tList = valTempList.replace(re,"\n\n");
      this.psalmsValueList = tList.split(',');
    });
  }
}
