import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes} from '@angular/router';

import * as $ from 'jquery';

import {AppComponent} from './app.component';
import { DoxComponent } from './dox/dox.component';
import { SecondComponent } from './second/second.component';
import { LyricsComponent } from './lyrics/lyrics.component';
import { HymnsComponent } from './hymns/hymns.component';
import { PsalmsComponent } from './psalms/psalms.component';
import { WorshipComponent } from './worship/worship.component';

import {AngularFireModule} from '@angular/fire';
import {AngularFireDatabaseModule} from '@angular/fire/database';

import {environment} from '../environments/environment';

const appRoutes: Routes = [
  {
      path: 'dox',
      component: DoxComponent
  },  
  {
      path: 'second',
      component: SecondComponent
  },
  {
      path: 'lyrics',
      component: LyricsComponent
  },
  {
      path: 'hymns',
      component: HymnsComponent 
  },
  {
      path: 'psalms',
      component: PsalmsComponent
  },
  {
      path: 'worship',
      component: WorshipComponent
  },
  {
      path: '',
      redirectTo: '/worship',
      pathMatch: 'full'
  },
  {
      path: '**',
      redirectTo: '/worship',
      pathMatch: 'full'
  }
];

@NgModule({
  declarations: [
    AppComponent,
    DoxComponent,
    SecondComponent,
    LyricsComponent,
    HymnsComponent,
    PsalmsComponent,
    WorshipComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    AngularFireModule.initializeApp(environment.firebaseConfig),
    AngularFireDatabaseModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})


export class AppModule {

}
